.accordion .accordion__content {
  font-family: var(--serif);
  padding: 20px 20px 25px;
}
.accordion .accordion__button {
  width: 100%;
  text-align: left;
  color: var(--gray);
  border: none;
  cursor: pointer;
  flex: 1 1 0;
  font-family: var(--sans-serif);
  font-weight: 400;
  line-height: 1.4;
  padding: 20px;
  margin: 0;
  background: var(--light-gray);
  transition: 0.2s;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 18px;
}
.accordion .accordion__button:hover {
  background: #e1e1e1;
}
.accordion .accordion__button .icon {
  align-items: center;
  background: var(--red);
  border-radius: 50%;
  color: currentColor;
  height: 24px;
  justify-content: center;
  right: 0;
  transform: rotate(-180deg) translate(0, -0.1em);
  transition: 0.4s;
  transition-property: transform, background-color;
  width: 24px;
  z-index: 100000000000000000;
  position: relative;
  display: block;
  fill: currentColor;
  margin-left: 35px;
}
.accordion .accordion__button .icon::before {
  background: var(--light-gray);
  content: "";
  height: 4px;
  left: calc(50% - 7px);
  position: absolute;
  top: calc(50% - 2px);
  transition: 0.2s;
  width: 14px;
  z-index: 2;
}
.accordion .accordion__button .icon::after {
  background: var(--light-gray);
  content: "";
  height: 4px;
  left: calc(50% - 7px);
  position: absolute;
  top: calc(50% - 2px);
  transition: 0.2s;
  width: 14px;
  transform: rotate(90deg);
  z-index: 1;
}
.accordion .accordion__button[aria-expanded=true] .icon,
.accordion .accordion__button[aria-expanded=true] svg {
  transform: rotateY(180deg);
}
.accordion .accordion__button[aria-expanded=true] .icon::after,
.accordion .accordion__button[aria-expanded=true] svg::after {
  z-index: 1;
  transform: rotate(0) !important;
}

.text {
  flex: 1;
}

